.node {
  width: 25px;
  height: 25px;
  outline: 1px solid grey;
  display: inline-block;
}

.node-start {
  background-color: green;
}

.node-end {
  background-color: red;
}

.node-visited {
  background-color: yellow;
}

.node-wall {
  background-color: blue;
}

.node-shortest-path {
  background-color: blueviolet;
}